export default {
  props: {
    progress: {
      type: Number,
      default: 0

    }
  },
  created(){
  },
  computed:{
    progressStyle(){
      switch (this.progress){
        case 0:
            break;
        case 1:
            return 'width:50%';
            break;
        case 2:
            return 'width:100%';
            break
      }
    }
  },
  methods: {
    iconStyle(flag){
      return `color:${this.progress >= flag ? '' : '#DCDCDC'}`;
    },
    iconClass(flag){
      return this.progress >= flag ? 'icon-selected' : 'icon-yuan';
    }
  }
}



